"use client"

import { useState } from "react";

export default function FormPage() {
    const [view, setView] = useState("login");

    return (
        <div className="flex flex-col min-h-screen">
          <main className="flex-grow pt-20">
            {view === "RegisterForm" && <FormPage/>}
          </main>
        </div>
    );
}


